import { CookieService } from 'angular2-cookie/core';
import { ToastController } from 'ionic-angular';
import { LoginServiceProvider } from '../login-service/login-service';
import { Injectable } from "@angular/core";
import { HttpInterceptor, HttpRequest, HttpHandler, HttpSentEvent, HttpHeaderResponse, HttpProgressEvent, HttpResponse, HttpUserEvent, HttpErrorResponse } from "@angular/common/http";
import { Observable } from "rxjs/Observable";
import 'rxjs/add/operator/catch';
import 'rxjs/add/observable/throw';
import 'rxjs/add/operator/switchMap';
import 'rxjs/add/operator/finally';
import 'rxjs/add/operator/filter';
import 'rxjs/add/operator/take';

@Injectable()
export class SMHttpInterceptor implements HttpInterceptor {

    constructor(private cookieService: CookieService, private loginService: LoginServiceProvider, public toastCtrl: ToastController) { }

    intercept(req: HttpRequest<any>, next: HttpHandler):
    Observable<HttpSentEvent | HttpHeaderResponse |
    HttpProgressEvent | HttpResponse<any> | HttpUserEvent<any>> {
        //this.toastCtrl.create({ message: 'Interceptor - AccessToken: ' + this.cookieService.get("accessToken"), position: 'botton', duration: 3000 }).present();
        let authReq = req.clone({
            headers: req.headers.set('Authorization', 'Bearer ' + this.cookieService.get("accessToken"))
        });

        //this.toastCtrl.create({ message: 'Interceptor - req Keys: ' + authReq.headers.keys(), position: 'botton', duration: 3000 }).present();
        
        return next.handle(authReq)
                .catch(error => {
                        if (error instanceof HttpErrorResponse) {
                            switch ((<HttpErrorResponse>error).status) {
                                case 401:
                                    this.toastCtrl.create({ message: 'Interceptor - Error 401', position: 'botton', duration: 3000 }).present();
                                    return this.getAccessToken(req, next);
                                case 0:
                                    this.toastCtrl.create({ message: 'Interceptor - Error 0', position: 'botton', duration: 3000 }).present();
                                    return this.getAccessToken(req, next);    
                            }
                            this.toastCtrl.create({ message: 'Interceptor - Error: ' + error.error, position: 'botton', duration: 3000 }).present();
                            Observable.throw(error);
                        }else{
                            this.toastCtrl.create({ message: 'Interceptor - Error: ' + error.error, position: 'botton', duration: 3000 }).present();
                            Observable.throw(error);
                        }
                    }
                );
    }

    private getAccessToken(req: HttpRequest<any>, next: HttpHandler): Observable<any> {
        //this.toastCtrl.create({ message: 'Interceptor - RefreshToken: ' + this.cookieService.get("refreshToken"), position: 'botton', duration: 3000 }).present();
        this.loginService.getAccessToken(this.cookieService.get("refreshToken")).switchMap(
            resp => {
                this.cookieService.put("accessToken", resp.access_token);
                //this.toastCtrl.create({ message: 'Interceptor - AccessToken: ' + this.cookieService.get("accessToken"), position: 'botton', duration: 3000 }).present();
                return next.handle(req.clone({
                    setHeaders:
                        { Authorization: 'Bearer ' + this.cookieService.get("accessToken")}
                }));                
            }
        ) .catch (err => this.toastCtrl.create({ message: 'Interceptor - getAccessToken Error: ' + err.error, position: 'botton', duration: 3000 }).present());
        
        return next.handle(req.clone({
            setHeaders:
                { Authorization: 'Bearer ' + this.cookieService.get("accessToken") }
        }));
    }

}


