import { FornecedorServiceProvider } from './../../providers/fornecedor-service/fornecedor-service';
import { TabsPage } from './../tabs/tabs';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, InfiniteScroll } from 'ionic-angular';
import { ViewChild } from '@angular/core';

@IonicPage()
@Component({
  selector: 'page-fornecedor-list',
  templateUrl: 'fornecedor-list.html',
  providers: [FornecedorServiceProvider]
})
export class FornecedorListPage {
  fornecedores: any;
  page: number;
  @ViewChild(InfiniteScroll) infiniteScroll: InfiniteScroll;

  constructor(public navCtrl: NavController, public navParams: NavParams, private toast: ToastController, private fornecedorProvider: FornecedorServiceProvider) { }

  ionViewDidEnter() {
    this.fornecedores = [];
    this.page = 1;
    this.infiniteScroll.enable(true);
    this.getAllFornecedor(this.page);
  }

  getAllFornecedor(page: number) {
    
    this.fornecedorProvider.listAll().subscribe(
      response => this.fornecedores = response
    );
  }

  getFornecedor() {
    setTimeout(() => {
      this.page += 1;
      this.getAllFornecedor(this.page);
    }, 500);
  }

  openFornecedor(id: number) {
    this.fornecedorProvider.get(id).subscribe(
      res => this.navCtrl.push('FornecedorEditPage', { fornecedor: res })
    );
  }

  openCreateFornecedor() {
    this.navCtrl.push('FornecedorEditPage');
  }

  openEditFornecedor(id: number) {
    this.fornecedorProvider.get(id).subscribe(
      res => this.navCtrl.push('FornecedorEditPage', { fornecedor: res }),
      (error: any) => {
        this.toast.create({ message: 'Erro ao recuperar o fornecedor. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }

  deleteFornecedor(fornecedor: any) {
    this.fornecedorProvider.remove(fornecedor.id).subscribe(
      (result: any) => {
        let index = this.fornecedores.indexOf(fornecedor);
        this.fornecedores.splice(index, 1);

        this.toast.create({ message: 'Fornecedor excluído com sucesso.', position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage);
      },
      (error: any) => {
        this.toast.create({ message: 'Erro ao excluir o fornecedor. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }
}


