import { HttpRequest } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { Utils } from './../../entity/Utils';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { ToastController } from 'ionic-angular';

@Injectable()
export class ProdutoServiceProvider {
  private produtoUrl: string;

  constructor(public http: HttpClient, public toast: ToastController) {
    this.produtoUrl = Utils.getUrlBackEnd() + "produto/"
  }

  createProduto(codigo: string, descricao: string, fornecedor: string, 
    custo: number, valorVenda: number, tipo: string, imagem:any) {
    return new Promise((resolve, reject) => {
      var data = {
        codigo: codigo,
        descricao: descricao,
        fornecedor: fornecedor,
        custo: custo,
        valorVenda: valorVenda,
        tipo: tipo,
        imagem: imagem
      };

      this.http.post(this.produtoUrl + 'register', data)
        .subscribe((result: any) => {
          resolve(result.json());
        },
        (error) => {
          reject(error.json());
        });
    });
  }

  public listAll() {
    return this.http.get(this.produtoUrl);
  }
  
  getAll(page: number) {
    return this.http.get(this.produtoUrl + '10/' + page);
  }

  get(id: number) {
    let url = this.produtoUrl + id;

    return this.http.get(url);
  }

  pushFilesToStorage(id: number, file1: File, file2: File, file3: File) {
    const formdata: FormData = new FormData();
 
    if (file1 != null)
      formdata.append('files', file1, file1.name);
    if (file2 != null)
      formdata.append('files', file2, file2.name);
    if (file3 != null)
      formdata.append('files', file3, file3.name);
    let uploadUrl = this.produtoUrl + id + "/enviarImagens";

    const req = new HttpRequest('POST', uploadUrl, formdata, {
      reportProgress: true,
      responseType: 'text'
    });
 
    return this.http.request(req);
  }

  pushFileToStorage1(id: number, file: File) {
    const formdata: FormData = new FormData();
 
    formdata.append('file', file);
    let uploadUrl = this.produtoUrl + id + "/enviarImagem1";

    const req = new HttpRequest('POST', uploadUrl, formdata, {
      reportProgress: true,
      responseType: 'text'
    });
 
    return this.http.request(req);
  }
  
  pushFileToStorage2(id: number, file: File) {
    const formdata: FormData = new FormData();
 
    formdata.append('file', file);
    let uploadUrl = this.produtoUrl + id + "/enviarImagem2";

    const req = new HttpRequest('POST', uploadUrl, formdata, {
      reportProgress: true,
      responseType: 'text'
    });
 
    return this.http.request(req);
  }

  pushFileToStorage3(id: number, file: File) {
    const formdata: FormData = new FormData();
 
    formdata.append('file', file);
    let uploadUrl = this.produtoUrl + id + "/enviarImagem3";

    const req = new HttpRequest('POST', uploadUrl, formdata, {
      reportProgress: true,
      responseType: 'text'
    });
 
    return this.http.request(req);
  }


  insert(produto: any) {
    let url = this.produtoUrl;
    return this.http.post(url, produto);
  }

  updateFornecedor(id: number, fornecedor_id: number) {
    let url = this.produtoUrl + 'updForn/' + id + '/' + fornecedor_id;
    return this.http.put(url, fornecedor_id);
  }

  addItem(id: number, item_id: number) {
    let url = this.produtoUrl + 'addItem/' + id + '/' + item_id;
    return this.http.put(url, item_id);
  }

  update(produto: any) {
    let url = this.produtoUrl + produto.id;
    return this.http.put(url, produto);
  }

  remove(id: number) {
    let url = this.produtoUrl + id;
    return this.http.delete(url);
  }
}



