import { HttpRequest } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { Utils } from './../../entity/Utils';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { ToastController } from 'ionic-angular';
import { Fornecedor } from '../../entity/Fornecedor';

@Injectable()
export class ItemServiceProvider {
  private itemUrl: string;

  constructor(public http: HttpClient, public toast: ToastController) {
    this.itemUrl = Utils.getUrlBackEnd() + "item/"
  }

  createItem(codigo: string, descricao: string, fornecedor: Fornecedor, 
    codFornecedor: string, custo: number, tipo: string, imagem1:string) {
    return new Promise((resolve, reject) => {
      var data = {
        codigo: codigo,
        descricao: descricao,
        fornecedor: fornecedor,
        codFornecedor: codFornecedor,
        custo: custo,
        tipo: tipo,
        imagem1: imagem1
      };

      this.http.post(this.itemUrl + 'register', data)
        .subscribe((result: any) => {
          resolve(result.json());
        },
        (error) => {
          reject(error.json());
        });
    });
  }

  public listAll() {
    return this.http.get(this.itemUrl);
  }
  
  getAll(page: number) {
    return this.http.get(this.itemUrl + '10/' + page);
  }

  get(id: number) {
    let url = this.itemUrl + id;

    return this.http.get(url);
  }

  pushFilesToStorage(id: number, file1: File) {
    const formdata: FormData = new FormData();
 
    if (file1 != null)
      formdata.append('files', file1, file1.name);

    let uploadUrl = this.itemUrl + id + "/enviarImagens";

    const req = new HttpRequest('POST', uploadUrl, formdata, {
      reportProgress: true,
      responseType: 'text'
    });
 
    return this.http.request(req);
  }

  insert(item: any) {
    let url = this.itemUrl;
    return this.http.post(url, item);
  }

  updateFornecedor(id: number, fornecedor_id: number) {
    let url = this.itemUrl + 'updForn/' + id + '/' + fornecedor_id;
    return this.http.put(url, fornecedor_id);
  }

  update(item: any) {
    let url = this.itemUrl + item.id;
    return this.http.put(url, item);
  }

  remove(id: number) {
    let url = this.itemUrl + id;
    return this.http.delete(url);
  }
}



