import { PerfilServiceProvider } from './../../providers/perfil-service/perfil-service';
import { Component, Injectable } from '@angular/core';
import { NavController, ToastController } from 'ionic-angular';

@Component({
  selector: 'perfil-page',
  templateUrl: 'perfil.html',
  providers: [PerfilServiceProvider]
})
@Injectable()
export class PerfilPage {
  public perfis: any;
  constructor(public navCtrl: NavController, 
              public perfilService: PerfilServiceProvider,
              public toastCtrl: ToastController) {

  }

  ionViewWillEnter() {
    this.perfis = [];
    this.toastCtrl.create({ message: 'Getting Perfis', position: 'botton', duration: 3000 }).present();
    this.perfilService.getPerfis().subscribe(
      response => { 
        this.perfis = response;
        this.toastCtrl.create({ message: 'Perfil[0]: ' + this.perfis[0].nome, position: 'botton', duration: 3000 }).present(),
        (err) => this.toastCtrl.create({ message: 'Perfil.ts - getPerfis Erro: ' + err.error, position: 'botton', duration: 3000 }).present()
      }
    );
    
  }

}


