import { HttpClient } from '@angular/common/http';
import { Utils } from './../../entity/Utils';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { ToastController } from 'ionic-angular';

@Injectable()
export class FornecedorServiceProvider {
  private fornecedorUrl: string;

  constructor(public http: HttpClient, public toast: ToastController) {
    this.fornecedorUrl = Utils.getUrlBackEnd() + "fornecedor/"
  }

  createItem(nomeFantasia: string, razaoSocial: string, cnpj: string, 
    endereco: string, refEnd: string, bairro: string, cidade: string,
    estado: string, telefone: string, contato: string, celular: string, 
    email: string, site: string, referencia1: string) 
  {
    return new Promise((resolve, reject) => {
      var data = {
        nomeFantasia: nomeFantasia,
        razaoSocial: razaoSocial,
        cnpj: cnpj,
        endereco: endereco,
        refEnd: refEnd,
        bairro: bairro,
        cidade: cidade,
        estado: estado,
        telefone: telefone,
        contato: contato,
        celular: celular,
        email: email,
        site: site,
        referencia1: referencia1
      };

      this.http.post(this.fornecedorUrl + 'register', data)
        .subscribe((result: any) => {
          resolve(result.json());
        },
        (error) => {
          reject(error.json());
        });
    });
  }

  public listAll() {
    return this.http.get(this.fornecedorUrl);
  }
  
  getAll(page: number) {
    return this.http.get(this.fornecedorUrl + '10/' + page);
  }

  get(id: number) {
    let url = this.fornecedorUrl + id;

    return this.http.get(url);
  }

  insert(fornecedor: any) {
    let url = this.fornecedorUrl;
    return this.http.post(url, fornecedor);
  }

  update(fornecedor: any) {
    let url = this.fornecedorUrl + fornecedor.id;
    return this.http.put(url, fornecedor);
  }

  remove(id: number) {
    let url = this.fornecedorUrl + id;
    return this.http.delete(url);
  }
}


