import { ProdutoServiceProvider } from './../../providers/produto-service/produto-service';
import { TabsPage } from './../tabs/tabs';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, InfiniteScroll } from 'ionic-angular';
import { ViewChild } from '@angular/core';

@IonicPage()
@Component({
  selector: 'page-produto-list',
  templateUrl: 'produto-list.html',
  providers: [ProdutoServiceProvider]
})
export class ProdutoListPage {
  produtos: any;
  showImage: boolean;
  fullScreenImage: String;
  page: number;
  @ViewChild(InfiniteScroll) infiniteScroll: InfiniteScroll;

  constructor(public navCtrl: NavController, public navParams: NavParams, 
              private toast: ToastController, 
              private produtoProvider: ProdutoServiceProvider) { }

  ionViewDidEnter() {
    this.produtos = [];
    this.page = 1;
    this.infiniteScroll.enable(true);
    this.showImage = false;
    this.getAllProduto(this.page);
  }

  showFullscreenImage(enable: boolean) {
    this.showImage = enable
  }

  /*openImage(id: number) {
    let produto: Produto;
    this.produtoProvider.get(id).subscribe(
      (response: Produto) => produto = response,
      () => {},
      () => {
        this.toast.create({ message: 'Produto: ' + produto.codigo, position: 'botton', duration: 3000 }).present();    
        this.fullScreenImage = produto.imagem1;
        this.showFullscreenImage(true);
      }

    );
    
  }*/

  getAllProduto(page: number) {    
    this.produtoProvider.listAll().subscribe(
      response => this.produtos = response
    );
  }

  getProduto() {
    setTimeout(() => {
      this.page += 1;
      this.getAllProduto(this.page);
    }, 500);
  }

  openProdutoDetail(id: number) {
    this.produtoProvider.get(id).subscribe(
      res => this.navCtrl.push('ProdutoImagesPage', { produto: res })
    );
  }

  openProduto(id: number) {
    this.produtoProvider.get(id).subscribe(
      res => this.navCtrl.push('ProdutoEditPage', { produto: res })
    );
  }

  openCreateProduto() {
    this.navCtrl.push('ProdutoEditPage');
  }

  openEditProduto(id: number) {
    this.produtoProvider.get(id).subscribe(
      res => this.navCtrl.push('ProdutoEditPage', { produto: res }),
      (error: any) => {
        this.toast.create({ message: 'Erro ao recuperar o produto. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }

  deleteProduto(produto: any) {
    this.produtoProvider.remove(produto.id).subscribe(
      (result: any) => {
        let index = this.produtos.indexOf(produto);
        this.produtos.splice(index, 1);

        this.toast.create({ message: 'Produto excluído com sucesso.', position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage);
      },
      (error: any) => {
        this.toast.create({ message: 'Erro ao excluir o produto. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }
}


