import { HttpRequest } from '@angular/common/http';
import { HttpClient } from '@angular/common/http';
import { Utils } from './../../entity/Utils';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';
import { ToastController } from 'ionic-angular';

@Injectable()
export class KitServiceProvider {
  private kitUrl: string;

  constructor(public http: HttpClient, public toast: ToastController) {
    this.kitUrl = Utils.getUrlBackEnd() + "kit/"
  }

  createKit(id: number, nome: string, descricao: string) {
    return new Promise((resolve, reject) => {
      var data = {
        id: id,
        nome: nome,
        descricao: descricao,
      };

      this.http.post(this.kitUrl + 'register', data)
        .subscribe((result: any) => {
          resolve(result.json());
        },
        (error) => {
          reject(error.json());
        });
    });
  }

  public listAll() {
    return this.http.get(this.kitUrl);
  }
  
  getAll(page: number) {
    return this.http.get(this.kitUrl + '10/' + page);
  }

  get(id: number) {
    let url = this.kitUrl + id;

    return this.http.get(url);
  }

  insert(kit: any) {
    let url = this.kitUrl;
    return this.http.post(url, kit);
  }

  update(kit: any) {
    let url = this.kitUrl + kit.id;
    return this.http.put(url, kit);
  }

  remove(id: number) {
    let url = this.kitUrl + id;
    return this.http.delete(url);
  }

  addProduto(id: number, produto_id: number) {
    let url = this.kitUrl + 'addProduto/' + id + '/' + produto_id;
    return this.http.put(url, produto_id);
  }

}




