import { TabsPage } from './../tabs/tabs';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, InfiniteScroll } from 'ionic-angular';
import { ViewChild } from '@angular/core';
import { ItemServiceProvider } from '../../providers/item-service/item-service';

@IonicPage()
@Component({
  selector: 'page-item-list',
  templateUrl: 'item-list.html',
  providers: [ItemServiceProvider]
})
export class ItemListPage {
  itens: any;
  showImage: boolean;
  page: number;
  @ViewChild(InfiniteScroll) infiniteScroll: InfiniteScroll;

  constructor(public navCtrl: NavController, public navParams: NavParams, 
              private toast: ToastController, 
              private itemProvider: ItemServiceProvider) { }

  ionViewDidEnter() {
    this.itens = [];
    this.page = 1;
    this.infiniteScroll.enable(true);
    this.showImage = false;
    this.getAllItem(this.page);
  }

  showFullscreenImage(enable: boolean) {
    this.showImage = enable
  }

  getAllItem(page: number) {    
    this.itemProvider.listAll().subscribe(
      response => this.itens = response
    );
  }

  getItem() {
    setTimeout(() => {
      this.page += 1;
      this.getAllItem(this.page);
    }, 500);
  }

  openItem(id: number) {
    this.itemProvider.get(id).subscribe(
      res => this.navCtrl.push('ItemEditPage', { item: res })
    );
  }

  openCreateItem() {
    this.navCtrl.push('ItemEditPage');
  }

  openEditItem(id: number) {
    this.itemProvider.get(id).subscribe(
      res => this.navCtrl.push('ItemEditPage', { item: res }),
      (error: any) => {
        this.toast.create({ message: 'Erro ao recuperar item. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }

  deleteItem(item: any) {
    this.itemProvider.remove(item.id).subscribe(
      (result: any) => {
        let index = this.itens.indexOf(item);
        this.itens.splice(index, 1);

        this.toast.create({ message: 'Item excluído com sucesso.', position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage);
      },
      (error: any) => {
        this.toast.create({ message: 'Erro ao excluir item. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }
}


