import { HttpClient } from '@angular/common/http';
import { Utils } from './../../entity/Utils';
import { Injectable } from '@angular/core';
import 'rxjs/add/operator/map';

@Injectable()
export class RepresentanteServiceProvider {
  private representanteUrl: string;

  constructor(public http: HttpClient) {
    this.representanteUrl = Utils.getUrlBackEnd() + "representante/"
  }

  createRepresentante(nome: string, cpf: string, email: string, celular: string) {
    return new Promise((resolve, reject) => {
      var data = {
        nome: nome,
        cpf: cpf,
        email: email,
        celular: celular
      };

      this.http.post(this.representanteUrl + 'register', data)
        .subscribe((result: any) => {
          resolve(result.json());
        },
        (error) => {
          reject(error.json());
        });
    });
  }

  public listAll() {
    return this.http.get(this.representanteUrl);
  }
  
  getAll(page: number) {
    return this.http.get(this.representanteUrl + '10/' + page);
  }

  get(id: number) {
    let url = this.representanteUrl + id;

    return this.http.get(url);
  }

  insert(user: any) {
    let url = this.representanteUrl;

    return this.http.post(url, user);
  }

  update(representante: any) {
    let url = this.representanteUrl + representante.id;
    return this.http.put(url, representante);
  }

  remove(id: number) {
    let url = this.representanteUrl + id;

    return this.http.delete(url);
  }

  addKit(id: number, kit_id: number) {
    let url = this.representanteUrl + 'addKit/' + id + '/' + kit_id;
    return this.http.put(url, kit_id);
  }
}


