import { TabsPage } from './../tabs/tabs';
import { RepresentanteServiceProvider } from './../../providers/representante-service/representante-service';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, InfiniteScroll } from 'ionic-angular';
import { ViewChild } from '@angular/core';

@IonicPage()
@Component({
  selector: 'page-representante-list',
  templateUrl: 'representante-list.html',
  providers: [RepresentanteServiceProvider]
})
export class RepresentanteListPage {
  representantes: any;
  page: number;
  @ViewChild(InfiniteScroll) infiniteScroll: InfiniteScroll;

  constructor(public navCtrl: NavController, public navParams: NavParams, private toast: ToastController, private representanteProvider: RepresentanteServiceProvider) { }

  ionViewDidEnter() {
    this.representantes = [];
    this.page = 1;
    this.infiniteScroll.enable(true);
    this.getAllRepresentante(this.page);
  }

  getAllRepresentante(page: number) {
    
    this.representanteProvider.listAll().subscribe(
      response => this.representantes = response
    );
  }

  getRepresentante() {
    setTimeout(() => {
      this.page += 1;
      this.getAllRepresentante(this.page);
    }, 500);
  }

  openRepresentante(id: number) {
    this.representanteProvider.get(id).subscribe(
      res => this.navCtrl.push('RepresentanteEditPage', { representante: res })
    );
  }

  openCreateRepresentante() {
    this.navCtrl.push('RepresentanteEditPage');
  }

  openEditRepresentante(id: number) {
    this.representanteProvider.get(id).subscribe(
      res => this.navCtrl.push('RepresentanteEditPage', { representante: res }),
      (error: any) => {
        this.toast.create({ message: 'Erro ao recuperar o representante. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }

  deleteRepresentante(representante: any) {
    this.representanteProvider.remove(representante.id).subscribe(
      (result: any) => {
        let index = this.representantes.indexOf(representante);
        this.representantes.splice(index, 1);

        this.toast.create({ message: 'Representante excluído com sucesso.', position: 'botton', duration: 3000 }).present();
        this.navCtrl.setRoot(TabsPage);
      },
      (error: any) => {
        this.toast.create({ message: 'Erro ao excluir o representante. Erro: ' + error.error, position: 'botton', duration: 3000 }).present();
      }
    );
  }
}


