import { KitServiceProvider } from './../providers/kit-service/kit-service';
import { FornecedorListPage } from './../pages/fornecedor-list/fornecedor-list';
import { ItemListPage } from './../pages/item-list/item-list';
import { EstoquePage } from './../pages/estoque/estoque';
import { ProdutoListPage } from './../pages/produto-list/produto-list';
import { FileTransfer, FileTransferObject } from '@ionic-native/file-transfer';
import { File } from '@ionic-native/file';
import { Camera } from '@ionic-native/camera';
import { ImagePicker } from '@ionic-native/image-picker';
import { Base64 } from '@ionic-native/base64';
import { PerfilPage } from './../pages/perfil/perfil';
import { CookieService } from 'angular2-cookie/core';
import { Utils } from './../entity/Utils';
import { HttpModule } from '@angular/http';
import { LoginPageModule } from './../pages/login/login.module';
import { NgModule, ErrorHandler } from '@angular/core';
import { BrowserModule } from '@angular/platform-browser';
import { IonicApp, IonicModule, IonicErrorHandler } from 'ionic-angular';
import { ComponentInicial } from './app.component';

import { AboutPage } from '../pages/about/about';
import { HomePage } from '../pages/home/home';
import { TabsPage } from '../pages/tabs/tabs';

import { StatusBar } from '@ionic-native/status-bar';
import { SplashScreen } from '@ionic-native/splash-screen';
import { LoginServiceProvider } from '../providers/login-service/login-service';
import { HttpClientModule, HTTP_INTERCEPTORS } from '@angular/common/http';
import { SMHttpInterceptor } from '../providers/sm-http-interceptor/sm-http-interceptor';
import { RepresentanteServiceProvider } from '../providers/representante-service/representante-service';
import { RepresentanteListPage } from '../pages/representante-list/representante-list';
import { ProdutoServiceProvider } from '../providers/produto-service/produto-service';
import { ComprasPage } from '../pages/compras/compras';
import { VendasPage } from '../pages/vendas/vendas';
import { RelatoriosPage } from '../pages/relatorios/relatorios';
import { ItemServiceProvider } from '../providers/item-service/item-service';
import { FornecedorServiceProvider } from '../providers/fornecedor-service/fornecedor-service';

@NgModule({
  declarations: [
    ComponentInicial,
    AboutPage,
    PerfilPage,
    ItemListPage,
    ProdutoListPage,    
    FornecedorListPage,
    RepresentanteListPage,
    ComprasPage,
    VendasPage,
    EstoquePage,
    RelatoriosPage,
    HomePage,
    TabsPage
  ],
  imports: [
    BrowserModule,
    HttpClientModule,
    HttpModule,
    LoginPageModule,
    IonicModule.forRoot(ComponentInicial)
  ],
  bootstrap: [IonicApp],
  entryComponents: [
    ComponentInicial,
    AboutPage,
    PerfilPage,
    ItemListPage,
    ProdutoListPage,
    FornecedorListPage,
    RepresentanteListPage,
    ComprasPage,
    VendasPage,
    EstoquePage,
    RelatoriosPage,
    HomePage,
    TabsPage
  ],
  providers: [
    StatusBar,
    CookieService,
    SplashScreen,
    Utils,
    {
      provide:HTTP_INTERCEPTORS, useClass: SMHttpInterceptor, multi: true
    },
    { provide: ErrorHandler, useClass: IonicErrorHandler },
    LoginServiceProvider,
    RepresentanteServiceProvider,
    ImagePicker,
    Base64,
    ProdutoServiceProvider,
    FileTransfer,
    FileTransferObject,
    File,
    Camera,
    ItemServiceProvider,
    FornecedorServiceProvider,
    KitServiceProvider
  ]
})
export class AppModule { }


